import React, { Component } from 'react'
import { Col, Container, Row } from 'reactstrap'
import { Card, CardText, CardBody, CardTitle, Button } from 'reactstrap'

class RequestSuccessView extends Component {
  render() {
    return (
      <Container className="mt-3" data-test="RequestSuccessView">
        <Row>
          <Col>
            <Card>
              <CardBody>
                <CardTitle>We have recieved your Request!</CardTitle>
                <CardText>
                  Thank you for your request. We will get back to you within 48
                  hours.
                </CardText>
                <Button onClick={() => this.props.history.push('/')}>
                  Back to Home
                </Button>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Container>
    )
  }
}

export default RequestSuccessView



